import Accordion from "./Accordion";
import React from 'react';
import {useGetData} from "../Providers/CourseProvider";
import CMSExercises from "./CMSExercises";

const CMSModules = ({setState, id}) => {
    const modules = useGetData('Modules', id ?? '0');

    if(!Array.isArray(modules)) return null;

    return modules.map(({id, title, ...data}) => {
        const {type, ...props} = {id: id, type: {remove: 'module', add: 'exercise', edit: 'module'}};

        return (
            <Accordion
                key={id}
                title={title}
                add={() => setState({...props, type: type.add, action: 'add'})}
                remove={() => setState({...props, type: type.remove, action: 'remove'})}
                edit={() => setState({...props, type: type.edit, action: 'edit', data: {...data, title}})}
                symbol={'book'}
            >
                <CMSExercises id={id} setState={setState} />
            </Accordion>
        )
    });
};

export default CMSModules;

