import React from 'react';
import {
    useAddData, useDeleteData, useUpdateData
} from "../Providers/CourseProvider";
import Input from "../Atomics/Input";
import Section from "../Atomics/Section";
import Form from "../Atomics/Form";
import TextArea from "../Atomics/TextArea";
import ExerciseEditor from "../Editor";
import Accordion from "./Accordion";
import CMSCourses from "./CMSCourses";
import SymbolSelection from "../Atomics/SymbolSelection";
import MultipleChoice from "../Editor/MultipleChoice";

const CMS = () => {
    const [state, setState] = React.useState({});

    const constructInputs = (fields, data) => {
        const temp = [];

        for(let key in fields) {
            switch(fields[key][0]) {
                case 'textarea':
                    temp.push(<TextArea value={data?.[key]} key={key} name={key} placeholder={fields[key][1]} />);
                    break;
                case 'input':
                    temp.push(<Input value={data?.[key]} key={key} name={key} placeholder={fields[key][1]} />);
                    break;
                case 'symbol':
                    temp.push(<SymbolSelection key={key} icon={data?.[key] ?? 'add'} name={key} />);
                    break;
                default:
                    break;
            }
        };

        return temp;
    };

    const Add = ({type, fields, id}) => {
        const addData = useAddData();

        const handleSubmit = (state) => {
            switch(type) {
                case 'course':
                    addData('Courses', state);
                    break;
                case 'moduleGroup':
                    addData('ModuleGroups', state, id)
                    break;
                case 'module':
                    addData('Modules', state, id);
                    break;
                case 'exercise':
                    addData('Exercises', state, id);
                    break;
                default:
                    return;
            }

            setState({});
        };

        return (type !== 'exercise' ?
            <Form onSubmit={handleSubmit}>{constructInputs(fields)}</Form> :
            <ExerciseEditor onSubmit={handleSubmit}/>
        );
    };

    const Edit = ({type, fields, id, data}) => {
        const updateData = useUpdateData();

        const handleSubmit = (state) => {
            switch(type) {
                case 'course':
                    updateData('Courses', state, id);
                    break;
                case 'moduleGroup':
                    updateData('ModuleGroups', state, id);
                    break;
                case 'module':
                    updateData('Modules', state, id);
                    break;
                case 'exercise':
                    updateData('Exercises', state, id);
                    break;
                default:
                    return;
            }

            setState({});
        };

        return (type !== 'exercise' ?
                <Form onSubmit={handleSubmit}>{constructInputs(fields, data)}</Form> :
                <ExerciseEditor onSubmit={handleSubmit} id={id} />
        );
    };

    const Remove = ({type, id}) => {
        const deleteData = useDeleteData();

        let answer = window.confirm('Wirklich löschen?');

        if(!answer)  {
            setState({});
            return null;
        }

        switch(type) {
            case 'course':
                deleteData('Courses', id);
                break;
            case 'moduleGroup':
                deleteData('ModuleGroups', id);
                break;
            case 'module':
                deleteData('Modules', id);
                break;
            case 'exercise':
                deleteData('Exercises', id);
                break;
            default:
                return;
        }

        setState({});

        return null;
    };

    const renderComponent = ({action, type, ...props}) => {
        const typeFields = {
            course: {
                title: ['input', 'Titel'],
                description: ['textarea', 'Beschreibung'],
                symbol: ['symbol', 'Symbol']
            },
            moduleGroup: {
                title: ['input', 'Titel'],
            },
            module: {
                title: ['input', 'Titel'],
                description: ['textarea', 'Beschreibung'],
                symbol: ['symbol', 'Symbol']
            },
            exercise: {
                title: ['input', 'Titel'],
                type: ['input', 'Typ'],
                content: ['textarea', 'Inhalt'],
            }
        };

        switch(action) {
            case 'edit':
                return <Edit fields={typeFields[type]} type={type} {...props} />
            case 'add':
                return <Add fields={typeFields[type]} type={type} {...props} />
            case 'remove':
                return <Remove fields={typeFields[type]} type={type} {...props} />
            default:
                return null;
        }
    };

    return (
        <>
            <Accordion title={'Übersicht'}>
                <Accordion title={'Neuen Kurs hinzufügen'} add={() => setState({type: 'course', action: 'add'})} />
                <CMSCourses setState={setState} />
            </Accordion>
            <Section title={'Konsole'} style={{marginTop: 16}}>
                {renderComponent(state)}
            </Section>
        </>
    )
};

export default CMS;