import styled from "styled-components";
import React from "react";
import {injectPlaceholder} from "./Placeholder";
import Icon from "./Icon";
import useDimensions from "../Hooks/useDimensions";

const Base = styled.section`
    border-bottom: 1px solid #ccc;
    padding-bottom: 8px;
    margin-bottom: 16px;
    
    + :last-child {
        border: none;
    }
`;

const Title = styled.div`
    font-size: 16pt;
    margin-bottom: 16px;
    color: black;
    display: flex;
    align-items: center;
    justify-content: space-between;
    ${injectPlaceholder}
`;

const Body = styled.div`
    overflow: hidden;
`;

const Wrapper = styled.div`
    overflow: hidden;
    transition: 0.3s ease height;
    height: ${({height, active}) => active ? height : 0}px;
`;

const Section = ({title, children, isStatic, ...props}) => {
    const [state, setState] = React.useState(true);
    const [ref, dimensions] = useDimensions();

    return (
        <Base {...props}>
            <Title onClick={() => setState(state => !state)}>{title}{isStatic ? null : <Icon>{state ? 'remove' : 'add'}</Icon>}</Title>
            {isStatic ? <Body ref={ref}>
                            {children}
                        </Body>
                        :
                        <Wrapper height={dimensions.height} active={state}>
                            <Body ref={ref}>
                                {children}
                            </Body>
                        </Wrapper>
            }
        </Base>
    )
};

export default Section;