import React from 'react';
import {Editor, EditorState, RichUtils, AtomicBlockUtils, DefaultDraftBlockRenderMap, convertToRaw, convertFromRaw} from 'draft-js';
import useEditorBlockRenderer from './EditorBlockRenderer';
import Button from "../Atomics/Button";
import Icon from "../Atomics/Icon";
import Prompt from "./Prompt";
import styled from 'styled-components';
import Immutable from 'immutable'
import Section from "../Atomics/Section";
import Input from "../Atomics/Input";
import {useGetData} from "../Providers/CourseProvider";
import Select from "../Atomics/Select";
import Form from "../Atomics/Form";
import MultipleChoice from "./MultipleChoice";
import Disclaimer from "../Atomics/Disclaimer";

const Wrapper = styled.div`
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.25);
    margin-bottom: 16px;
    background-color: white;
    border-radius: 4px;
`;

const EditorButton = styled(Button)`
    padding: 8px;
`;

const ExerciseEditor = ({onSubmit, id, exerciseType = 'theory'}) => {
    const [editorState, setEditorState] = React.useState(EditorState.createEmpty());
    const [promptState, setPromptState] = React.useState({active: false, data: null});
    const [title, setTitle] = React.useState('');
    const [focusOnce, setFocusOnce] = React.useState(false);
    const [firstLoad, setFirstLoad] = React.useState(true);
    const [exerciseParameters, setExerciseParameters] = React.useState(exerciseType);
    const editorBlockRenderer = useEditorBlockRenderer();
    const editor = React.useRef();

    const exercise = useGetData('Exercise', id);

    if(exercise?.content && firstLoad) {
        setEditorState(EditorState.createWithContent(convertFromRaw(exercise.content)));
        setTitle(exercise.title);
        setFirstLoad(false);
    }

    React.useEffect(() => {
        if(!focusOnce) {
            focus();
            setFocusOnce(true);
        }
    }, [focusOnce])

    const onChange = editorState => {
        setEditorState(editorState);
    };

    const blockRenderMap = Immutable.Map({
        'atomic': {
            element: 'div',
        }
    });

    const extendedBlockRenderMap = DefaultDraftBlockRenderMap.merge(blockRenderMap);

    const focus = () => {
        window.setTimeout(() => editor.current.focus(), 0);
    };

    const handleKeyCommand = command => {
        const newState = RichUtils.handleKeyCommand(editorState, command);

        if(newState) {
            onChange(newState);
            return true;
        }

        return false;
    };

    const toggleBlockType = blockType => {
        onChange(
            RichUtils.toggleBlockType(
                editorState,
                blockType
            )
        );
    };

    const toggleInlineStyle = styleType => {
        onChange(
            RichUtils.toggleInlineStyle(
                editorState,
                styleType
            )
        );

        focus();
    };

    const addAtomicBlock = (atomicBlockType, atomicBlockData) => {
        const contentState = editorState.getCurrentContent();
        const contentStateWithEntity = contentState.createEntity(
            atomicBlockType,
            'IMMUTABLE',
            atomicBlockData
        );

        const entityKey = contentStateWithEntity.getLastCreatedEntityKey();

        const newState = EditorState.set(
            editorState,
            {currentContent: contentStateWithEntity}
        );

        onChange(AtomicBlockUtils.insertAtomicBlock(newState, entityKey, ' '));
    };

    const getSelectedBlockType = () => {
        const selection = editorState.getSelection();

        return editorState.getCurrentContent().getBlockForKey(selection.getStartKey()).getType();
    };

    const blockStyleButtons = {
        'unstyled': {
            icon: 'title',
        },
        'quote': {
            icon: 'format_quote',
        }
    };

    const constructBlockStyleButtons = () => {
        const temp = [];

        for(let key in blockStyleButtons) {
            temp.push(
                <EditorButton style={{backgroundColor: getSelectedBlockType() === key ? '#ccc' : 'white'}} key={key} onClick={() => toggleBlockType(key)}>
                    <Icon>
                        {blockStyleButtons[key].icon}
                    </Icon>
                </EditorButton>
            );
        }

        return temp;
    };

    const inlineStyleButtons = {
        'BOLD': {
            icon: 'format_bold',
        },
        'ITALIC': {
            icon: 'format_italic',
        },
        'UNDERLINE': {
            icon: 'format_underline',
        },
        'STRIKETHROUGH': {
            icon: 'format_strikethrough'
        }
    };

    const constructInlineStyleButtons = () => {
        const temp = [];

        for(let key in inlineStyleButtons) {
            temp.push(
                <EditorButton style={{backgroundColor: editorState.getCurrentInlineStyle().has(key) ? '#ccc' : 'white'}} key={key} onClick={() => toggleInlineStyle(key)}>
                    <Icon>
                        {inlineStyleButtons[key].icon}
                    </Icon>
                </EditorButton>
            )
        }
        return temp;
    };

    const atomicBlockButtons = {
        'image': {
            data: {
                url: 'Bildadresse',
                caption: 'Bildunterschrift'
            },
            icon: 'image',
        },
        'video': {
            data: {
                url: 'Videoadresse',
                title: 'Videotitel'
            },
            icon: 'movie'
        },
        'formula': {
            data: {
                formula: 'Formel'
            },
            icon: 'functions'
        }
    };

    const constructAtomicBlockButtons = () => {
        let temp = [];

        for(let key in atomicBlockButtons) {
            temp.push(
                <EditorButton key={key} onClick={() => getAtomicBlockData(atomicBlockButtons[key].data, key)}>
                    <Icon>
                        {atomicBlockButtons[key].icon}
                    </Icon>
                </EditorButton>
            )
        };

        return temp;
    };

    const getAtomicBlockData = (data, type) => {
        setPromptState({active: true, data: data, type: type});
    };

    const handlePrompt = (data) => {
        addAtomicBlock(promptState.type, data);
        setPromptState({active: false, data: null, type: null});
    };

    const renderExerciseFields = (exerciseType) => {
        switch(exerciseType) {
            case 'theory':
                return 'Für Theorie Aufgaben gibt es keine Einstellmöglichkeiten!';
            case 'multiple-choice':
                return <MultipleChoice />;
            case 'calculation':
                return 'Calc';
            default:
                return null;
        }
    };

    return (
        <>
            <Section title={'Aufgabentyp'} isStatic>
                <Disclaimer/>
                <Form onSubmit={console.log} onChange={(type) => setExerciseParameters(state => ({...state, type: type?.exerciseType}))}>
                    <Select name={'exerciseType'} value={'theory'}>
                        <option value={'theory'}>Theorie</option>
                        <option value={'multiple-choice'}>Multiple Choice</option>
                        <option value={'calculation'}>Rechnen</option>
                    </Select>
                </Form>
            </Section>

            <Section title={'Aufgabenfelder'} isStatic>
                <Disclaimer/>
                {renderExerciseFields(exerciseParameters.type)}
            </Section>
            <Section isStatic title={
                <Input unstyled value={title} onChange={(e) => setTitle(e.target.value)} placeholder={'Aufgabentitel...'}/>
            }>
                <Wrapper>
                    {constructBlockStyleButtons()}


                    {constructInlineStyleButtons()}
                    {constructAtomicBlockButtons()}
                </Wrapper>
                <Editor blockRendererFn={editorBlockRenderer}
                        blockRenderMap={extendedBlockRenderMap}
                        editorState={editorState}
                        ref={editor}
                        onChange={onChange}
                        handleKeyCommand={handleKeyCommand}
                />
                <Prompt active={promptState.active} data={promptState.data} onSubmit={handlePrompt}/>
                <Button styled onClick={() => onSubmit({title: title, content: convertToRaw(editorState.getCurrentContent())})}>
                    Speichern!
                </Button>
            </Section>
        </>
    )
};

export default ExerciseEditor;