import Accordion from "./Accordion";
import React from 'react';
import {useGetData} from "../Providers/CourseProvider";
import CMSModules from "./CMSModules";

const CMSModuleGroups = ({setState, id}) => {
    const moduleGroups = useGetData('ModuleGroups', id ?? '0');

    if(!Array.isArray(moduleGroups)) return null;

    return moduleGroups.map(({id, title, ...data}) => {
        const {type, ...props} = {id: id, type: {remove: 'moduleGroup', add: 'module', edit: 'moduleGroup'}};

        return (
            <Accordion
                key={id}
                title={title}
                add={() => setState({...props, type: type.add, action: 'add'})}
                remove={() => setState({...props, type: type.remove, action: 'remove'})}
                edit={() => setState({...props, type: type.edit, action: 'edit', data: {...data, title}})}
                symbol={'collections_bookmark'}
            >
                <CMSModules id={id} setState={setState} />
            </Accordion>
        );
    });
};

export default CMSModuleGroups;

