import React from 'react';
import styled from "styled-components";
import Button from "../Atomics/Button";
import Icon from "../Atomics/Icon";

const Title = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.25);
    padding: 8px;
    margin-top: 8px;
    background-color: white;
    border-radius: 4px;
`;

const Container = styled.div`
    overflow: hidden;
    transition: 0.3s ease height, 0.3s ease padding;
`;

const Menu = styled.div`
    position: absolute;
    opacity: ${props => props.active ? 1 : 0};
    transition: 0.3s ease opacity, transform 0.3s ease;
    background-color: white;
    transform: ${props => props.active ? 'scale(1)' : 'scale(0)'} translateX(-4px) translateY(-4px);
    border-radius: 4px;
    overflow: hidden;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.25);
`;

const Wrapper = styled.div`
    background-color: rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    position: relative;
`;

const Accordion = ({title, children, add, remove, edit, moveUp, moveDown, symbol}) => {
    const [state, setState] = React.useState(false);
    const [menu, setMenu] = React.useState(false);
    const measure = React.useRef();


    const handleClick = event => {
        if(children === undefined || children.length === 0) return;
        setState(state => !state);

        event.stopPropagation();
    };

    return (
        <Wrapper>
            <div onMouseEnter={() => setMenu(true)} onMouseLeave={() => setMenu(false)}>
                <Menu active={menu}>
                    {add ?
                        <Button onClick={add}>
                            <Icon>
                                add
                            </Icon>
                        </Button> : null
                    }
                    {remove ?
                        <Button onClick={remove}>
                            <Icon>
                                remove
                            </Icon>
                        </Button> : null
                    }
                    {edit ?
                        <Button onClick={edit}>
                            <Icon>
                                edit
                            </Icon>
                        </Button> : null
                    }
                    {moveUp ?
                        <Button onClick={moveUp}>
                            <Icon>
                                keyboard_arrow_up
                            </Icon>
                        </Button> : null
                    }
                    {moveDown ?
                        <Button onClick={moveDown}>
                            <Icon>
                                keyboard_arrow_down
                            </Icon>
                        </Button> : null
                    }
                </Menu>
                <Title active={state} onClick={handleClick}>
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <Icon style={{marginRight: 8}}>
                            {symbol}
                        </Icon>
                        {title}
                    </div>
                    {children === undefined || children.length === 0 ? null : (state ? <Icon>remove</Icon> : <Icon>add</Icon>)}
                </Title>
            </div>
            <Container style={{height: state ? 'auto' : 0, padding: state ? '8px 16px 8px 16px' : 0}}>
                <div ref={measure}>
                    {children}
                </div>
            </Container>
        </Wrapper>
    )
};

export default Accordion;