import React from 'react';
import Input from "../Atomics/Input";
import Form from "../Atomics/Form";
import Button from "../Atomics/Button";
import Icon from "../Atomics/Icon";
import removeElementFromArray from "../Functions/removeElementFromArray";
import uniqueId from "../Functions/uniqueId";

const AddRemoveInput = ({remove, ...props}) => {
    return (
        <div>
            <Input {...props} />
            <Button onClick={remove}>
                <Icon>
                    remove
                </Icon>
            </Button>
        </div>
    )
};

const MultipleChoice = () => {
    const [state, setState] = React.useState({question: null, answers: []});

    const addAnswer = () => {
        setState(state => ({...state, answers: state.answers.concat({content: '', key: uniqueId()})}));
    };

    const removeAnswer = (index) => {
        setState(state => ({...state, answers: removeElementFromArray(state.answers, index)}))
    };

    const renderAnswers = () => {
        return state.answers.map((item, index) => <AddRemoveInput key={item.key} name={index} remove={() => removeAnswer(index)} />);
    };

    const updateAnswers = (answers) => {
        const temp = [];

        for(let item in answers) {
            temp.push(answers[item]);
        }

        setState(state => ({...state, answers: temp}));
    };

    return (
        <>
            <Input placeholder={'Frage'}/>
            <br />
            <Form onChange={updateAnswers}>
                {renderAnswers()}
            </Form>
            <br />
            <Button styled onClick={addAnswer}>
                Neue Antwort hinzufügen.
                <Icon>
                    add
                </Icon>
            </Button>
        </>
    )
}

export default MultipleChoice;