const Base = styled.div`
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    position: fixed;
    background-color: rgba(0, 0, 0, 0.2);
    z-index: 1300;
    display: flex;
    align-items: center;
    justify-content: center;
`;

const Wrapper = styled.div`
    max-width: 960px;
    width: 80%;
    height: 50vh;
    background-color: white;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    display: flex;
    flex-direction: column;
`;

const IconContainer = styled.div`
    overflow: auto;
    padding: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
`;

const Title = styled.h4`
    padding: 0 8px;
    text-align: center;
`;

const SymbolSelection = ({icon, onChange, name}) => {
    const [state, setState] = React.useState({open: false, icon: icon});

    const handleClick = icon => {
        setState(state => ({open: false, icon: icon}));
        onChange({target: {name: name, value: icon}});
    };

    const Icons = availableSymbols.map(item => {
        return <Icon style={{padding: 8}} onClick={(event) => {
            event.stopPropagation();
            handleClick(item);
        }} key={item}>{item}</Icon>
    });

    return (
        <>
            <Button styled type={'button'} onClick={() => {
                setState(state => ({...state, open: true}));
            }} style={{margin: 4, display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                <Icon style={{marginRight: 8}}>{state.icon}</Icon>
                Icon wählen
            </Button>
            {state.open ? (
                <Base onClick={() => handleClick(state.icon)}>
                    <Wrapper>
                        <Title>
                            Wählen Sie ein Icon aus.
                        </Title>
                        <IconContainer>
                            {Icons}
                        </IconContainer>
                    </Wrapper>
                </Base>) :
            null}
        </>)
};

export default SymbolSelection;