import Accordion from "./Accordion";
import React from 'react';
import {useGetData} from "../Providers/CourseProvider";
import CMSModuleGroups from "./CMSModuleGroups";

const CMSCourses = ({setState}) => {
    const courses = useGetData('Courses');

    if(!Array.isArray(courses)) return null;

    return courses.map(({id, title, ...data}) => {
        const {type, ...props} = {id: id, type: {remove: 'course', add: 'moduleGroup', edit: 'course'}};
        return (
            <div key={id} style={{marginBottom: 16}}>
                <Accordion
                    title={title}
                    add={() => setState({...props, type: type.add, action: 'add'})}
                    remove={() => setState({type: type.remove, action: 'remove', ...props})}
                    edit={() => setState({type: type.edit, action: 'edit', data: {...data, title}, ...props})}
                    symbol={'school'}
                >
                    <CMSModuleGroups id={props.id} setState={setState} />
                </Accordion>
            </div>
        )
    });
};

export default CMSCourses;



