import Text from "../Atomics/Text";
import {EditorBlock} from "draft-js";
import React from "react";
import Atomic from "../Atomics/Atomic";
import Quote from "../Atomics/Quote";

const editorBlockRenderer = (contentBlock) => {
    const type = contentBlock.getType();

    switch(type) {
        case 'unstyled':
            return {
                component: (props) => {
                    return (
                        <Text>
                            <EditorBlock {...props} />
                        </Text>
                    )
                },
                editable: true,
            }
        case 'quote':
            return {
                component: (props) => {
                    return (
                        <Quote>
                            <EditorBlock {...props} />
                        </Quote>
                    )
                },
                editable: true,
            }
        case 'atomic':
            return {
                component: (props) => (
                    <Atomic {...props} />
                ),
                editable: false,
            }
        default:
            return null;
    };
};

const useEditorBlockRenderer = () => {
    return editorBlockRenderer;
};

export default useEditorBlockRenderer;