import Accordion from "./Accordion";
import React from 'react';
import {useGetData} from "../Providers/CourseProvider";

const CMSExercises = ({setState, id}) => {
    const exercises = useGetData('Exercises', id ?? '0');

    if(!Array.isArray(exercises)) return null;

    return exercises.map(({id, title, ...data}) => {
        const {type, ...props} = {id: id, type: {remove: 'exercise', add: 'none', edit: 'exercise'}};

        return (
            <Accordion
                key={id}
                title={title}
                remove={() => setState({...props, type: type.remove, action: 'remove'})}
                edit={() => setState({...props, type: type.edit, action: 'edit', data: {...data, title}})}
                symbol={'bookmark'}
            >
            </Accordion>
        )
    });
};

export default CMSExercises;

