import React from 'react';
import styled from 'styled-components';
import Input from "../Atomics/Input";
import Form from "../Atomics/Form";

const Container = styled.div`
    transition: all 0.3s ease;
    background-color: rgba(0, 0, 0, 0.25);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1300;
    border-radius: 4px;
`;

const Base = styled.div`
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.25);
    background-color: white;
    padding: 16px;
`;

const Title = styled.div`

`;

const Prompt = ({data, active, onSubmit, title}) => {
    const constructDataFields = () => {
        let temp = [];

        for(let key in data) {
            temp.push(
                <Input key={key} name={key} placeholder={data[key]}></Input>
            )
        }

        return temp;
    }

    return active ? (
        <Container>
            <Base>
                <Title>{title}</Title>
                <Form onSubmit={onSubmit}>
                    {constructDataFields()}
                </Form>
            </Base>
        </Container>
    ) : null;
};

export default Prompt;